html,body,div,span,h1,h2,a,s,ul{
  margin:0;
  padding:0;
  border:0;
  font-size:100%;
  font:inherit;
  vertical-align:baseline
}

body{
  line-height:1;
  background:#0F4C3A
}

ul{
  list-style:none
}

html,body{
  height:100%
}

#root{
  height:100%
}

@media (min-width: 768px) {
  .md\:block {
    display: block;
  }

  .md\:w-48 {
    width: 12rem;
  }
}

@media (min-width: 1024px) {
  .lg\:w-64 {
    width: 16rem;
  }
}

.App{
  width:100%;
  height:100%;
  text-align:center;
  color:#ECECEC;
  position:relative
}

.loader-container{
  -webkit-transform:translate(-50%, -50%);
  transform:translate(-50%, -50%)
}

.loader{
  border:5px solid #f3f3f3;
  border-top:5px solid #3498db;
  border-radius:50%;
  width:100%;
  height:100%;
  -webkit-animation:spin 2s linear infinite;
  animation:spin 2s linear infinite
}

@-webkit-keyframes spin{
  0%{
    -webkit-transform:rotate(0deg);
    transform:rotate(0deg)
  }

  80%{
    -webkit-transform:rotate(240deg);
    transform:rotate(240deg)
  }

  100%{
    -webkit-transform:rotate(360deg);
    transform:rotate(360deg)
  }
}

@keyframes spin{
  0%{
    -webkit-transform:rotate(0deg);
    transform:rotate(0deg)
  }

  80%{
    -webkit-transform:rotate(240deg);
    transform:rotate(240deg)
  }

  100%{
    -webkit-transform:rotate(360deg);
    transform:rotate(360deg)
  }
}

.search_container{
  width:100%;
  margin:1.5rem 0 1.5rem 0;
  position:relative;
  box-sizing:border-box
}

.search_taigi_input{
  display:block;
  color:black;
  margin:auto;
  position:relative;
  border:2px #BFBFBF solid;
  box-shadow:2px 2px rgba(0,0,0,0.2);
  border-radius:10px;
  padding:1.5rem;
  font-size:1.27rem
}

.search_taigi_input:focus{
  outline:none
}

.search_query{
  display:block;
  margin:auto;
  color:#ECECEC
}

.play{
  position:absolute;
  left:50%;
  -webkit-transform:translateX(-50%);
  transform:translateX(-50%);
  width:0;
  height:0;
  border-style:solid;
  border-width:0.75rem 0 0.75rem 1rem;
  border-color:transparent transparent transparent rgba(96, 103, 115, var(--tw-border-opacity))
}

